.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
}

.input-container {
    display: flex;
    justify-content: space-between; /* Aligns input and button to the edges */
    width: 100%; /* Full width */
    max-width: 400px; /* Set a max width */
}

input {
    flex: 1; /* Takes up available space */
    padding: 10px;
    margin-right: 10px; /* Space between input and button */
}

button {
    padding: 10px;
}

ul {
    list-style-type: none;
    padding: 0;
    width: 100%; /* Full width */
    max-width: 400px; /* Set a max width */
}

.todo-item {
    display: flex;
    justify-content: space-between; /* Aligns todo text and delete button */
    align-items: center;
    padding: 10px;
    border: 1px solid #ccc;
    margin: 5px 0;
}

.delete-button {
    color: red; /* Change the text color to red */
    background: none; /* Remove background if needed */
    border: none; /* Remove border */
    cursor: pointer; /* Change cursor to pointer */
    font-size: 1.2em; /* Adjust size if necessary */
    margin-left: 10px; /* Space between todo item and button */
}

